@use 'base/mixins' as m;
@use 'base/variables' as v;

.video {
	position: relative;
	max-height: 70rem;
	min-height: 50rem;
	overflow: hidden;

	.overlay {
		position: absolute;
		height: 100%;
		width: 100%;
		background: rgb(124, 0, 195);
		background: linear-gradient(
			270deg,
			rgba(124, 0, 195, 0.5858718487394958) 0%,
			rgba(255, 0, 121, 0.4206057422969187) 100%
		);

		display: flex;
		align-items: center;
	}

	video {
		display: block;
		width: auto;
		@include m.desktop {
			width: 100%;
		}
	}

	.contenido-video {
		z-index: 10;
		h2 {
			color: v.$blanco;
			font-size: 7rem;
			margin: 0;
		}

		p {
			background-color: v.$rosa;
			display: inline-block;
			color: v.$blanco;
			padding: 1rem 2rem;
			font-weight: 700;
			font-size: 2.4rem;
			margin: 0;
		}
	}
}
